<template>
  <div class="container">
    <div class="login-title">
      一日之计
    </div>
    <div class="login-subtitle">
      记录每日计划
    </div>
    <el-form ref="ruleForm" :model="form" :rules="rules">
      <el-form-item prop="username">
        <el-input v-model="form.username" prefix-icon="el-icon-user"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          v-model="form.password"
          prefix-icon="el-icon-lock"
          type="password"
          @keyup.enter.native="submitForm"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          @click="submitForm"
          class="login-btn"
          :loading="loading"
        >
          登 录
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      form: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.loading = true;
          this.$store
            .dispatch("user/login", this.form)
            .then(() => {
              this.$router.push({ path: this.redirect || "/" });
              this.loading = false;
            })
            .catch(() => {
              this.loading = false;
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.login-title {
  text-align: center;
  font-size: x-large;
  font-weight: bold;
}

.login-subtitle {
  margin: 10px 0 50px 0;
  color: #909399;
  text-align: center;
}

.container {
  padding: 250px 680px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.login-btn {
  width: 100%;
}
</style>
